<template>
  <v-app>
    <Navigation :api="api" :map="map"/>
    <v-content>
      <Map3D v-if="map.show3D" :api="api" :map="map" />
      <Map v-else :api="api" :map="map"/>
    </v-content>
  </v-app>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
import Navigation from "./components/Navigation";
import Map3D from "./components/Map3D";
import Map from "./components/Map";

export default {
  name: "App",
  components: {
    Navigation,
    Map3D,
    Map,
  },
  data: () => ({
    api: {
      url: "https://airportgreengardenmjq3nzy3odq0.the-estatech.com/api",
      filter: {
        block: "",
        status: "",
        type: "",
        dimensions: ""
      }
    },
    map: {
      show3D: false,
      base: {
        osm: true,
        satellite: false,
        val: "osm"
      },

      // agg_uti: {
      //   utilities: true,
      // },


    },

geo: {
      polygons: {
          data: [],
          backup: [],
          style: {}
        },
    },
  }),
  computed: {
    ...mapGetters(["getPlots"])
  },
  methods: {
    async loadPolygons() {
      var polygons;
      await axios.get("data/Plots_Approved.json").then(response => {
        polygons = response.data.features;
        polygons.forEach(polygon => {
          this.geo.polygons.data.push(polygon);
          this.geo.polygons.backup.push(polygon);
        });
        this.geo.polygons.isLoaded = true;
      });
      
    },
  },

  
  created() {
    this.$store.dispatch("fetchPlots", {
      url: this.api.url,
      params: {
        block: this.api.filter.block,
        status: this.api.filter.status,
        type: this.api.filter.type,
        dimensions: this.api.filter.dimensions
      }
    });
    this.$store.dispatch("fetchBlockList", {
      url: this.api.url
    });
  },
  mounted() {
    this.loadPolygons();
  }
};
</script>
