<template>
  <l-map
    ref="map"
    style="height: 100%; z-index: 0;"
    :zoom="getZoom"
    :center="getCenter"
    :center-control="true"
    :options="{zoomControl: false}"
    @update:zoom="zoomUpdate"
  >
  
  <l-control-scale position="bottomleft" :imperial="false" :metric="true"></l-control-scale>
  <l-control-zoom position="bottomright"  ></l-control-zoom>

    <!-- Recenter Map -->
    <l-control 
    position="bottomleft" 
    ><v-chip  @click="reset_map">
    <h4>Reset Map</h4>
    </v-chip> </l-control>


     <l-control position="topleft" class="my-5 mx-7">
     <v-chip
        class="d-flex justify-center"
        style="width:135px;"
        color="white"
        @click="toggleRightDrawer"
        v-if="this.$store.state.leftDrawer == false"
      >Filter Menu</v-chip>
    </l-control>

    <l-control position="topright" class="my-2 mx-7">
          <v-autocomplete
            hide-details
            rounded
            color="black"
            background-color="white"
            style="width:200px;"
            :items="districtList"
            v-model="selectedDistrict"
            label="Search Province"
            @input="applyFilter"
    ></v-autocomplete>

    <v-autocomplete
            hide-details
            rounded
            color="black"
            background-color="white"
            style="width:200px;"
            :items="typeList"
            v-model="selectedType"
            label="Search District"
            @input="applyFilter"
    ></v-autocomplete>

    <v-autocomplete
            hide-details
            rounded
            color="black"
            background-color="white"
            style="width:200px;"
            :items="hotspotList"
            v-model="selectedHotspot"
            label="Search Zone"
            @input="applyFilter"
          ></v-autocomplete>
      </l-control>

    <div v-if="map.base.val == 'osm'">
      <l-tile-layer :url="osm"></l-tile-layer>
    </div>
    <div v-if="map.base.val == 'satellite'">
      <l-tile-layer :url="satellite"></l-tile-layer>
    </div>

    <!-- <l-marker
        v-for="DR_HouseConnections in map.DR_HouseConnections.features"
            :key="DR_HouseConnections.index"
            :icon="icon_DR_HouseConnections"
            :lat-lng="[DR_HouseConnections.geometry.coordinates[1],DR_HouseConnections.geometry.coordinates[0]]" 
            :visible="getDR_Visibility"   
    >
              <l-tooltip>
                <h3> {{DR_HouseConnections.properties.district}} </h3>
                <hr>
                <h4> Zone: {{DR_HouseConnections.properties.zone}} </h4>
                <hr>
                <h4> Hotspot: {{DR_HouseConnections.properties.hotspot_no}} </h4>
                <hr>
                <h4> Type: {{DR_HouseConnections.properties.type}} </h4>
              </l-tooltip>
    </l-marker> -->
  <l-marker-cluster>
    <l-marker
        v-for="feature in filteredFeatures"
        :key="`${feature.properties.id}`"
        :icon="icon_DR_HouseConnections"
        :lat-lng="[feature.geometry.coordinates[1], feature.geometry.coordinates[0]]"
    
      >
        <l-tooltip>
          <h3>{{ feature.properties.District }}</h3>
          <hr>
          <h4> Zone: {{ feature.properties.Zone }} </h4>
          <hr>
          <h4> Collector Code: {{ feature.properties.CollectorCode }} </h4>
          <hr>
          <h4> Date: {{ feature.properties.Date }} </h4>
          <hr>
          <h4> FSW: {{ feature.properties.pop_type_fsw }} </h4>
          <hr>
          <h4> MSM: {{ feature.properties.pop_type_msm }} </h4>
          <hr>
          <h4> TG: {{ feature.properties.pop_type_tg }} </h4>
          <hr>
          <h4> PWID: {{ feature.properties.pop_type_pwid }} </h4>
        </l-tooltip>
    </l-marker>
  </l-marker-cluster>

    <l-marker
      v-if="getSelectedPlot.length >= 2"
      :lat-lng="getSelectedPlot"     
    ></l-marker>

  </l-map>
</template>

