        "pop_type_msm": false,
        "pop_type_tg": true,
        "pop_type_pwid": false
      },
      "geometry": {
        "type": "Point",
        "coordinates": [
          67.0283524,
          24.9197946
        ]
      }
    },
    {
      "type": "Feature",
      "properties": {
        "id": 2177,
        "_parent_index": 9047,
        "start": "9:21:23 PM",
        "end": "9:24:08 PM",
        "latitude": 24.8244651,
        "longitude": 67.1718654,
        "Province": "Sin",
        "District": "Karachi",
        "Zone": 6,
        "Date": "10/9/2023",
        "CollectorCode": 166,
        "pop_type_fsw": true,
        "pop_type_msm": false,
        "pop_type_tg": false,
        "pop_type_pwid": false
      },
      "geometry": {
        "type": "Point",
        "coordinates": [
          67.1718654,
          24.8244651
        ]
      }
    }
  ]
},
  selectedDistrict: null,
  selectedType: null,
  selectedHotspot: null,
      
  
      osm: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      satellite:
        "https://api.tiles.mapbox.com/v4/mapbox.satellite/{z}/{x}/{y}.png?access_token=pk.eyJ1IjoiYXNpZm5hd2F6IiwiYSI6ImNramJjb2hiNjdtemMzMXFqM2N5dHExbHYifQ.dIMTRBIFraRp703Cz93QTw",
      zoom: 8,
      center: [33.5645474312881, 72.8829990311902],
      colors: [
             { name: "School", color: "#eb975b", expanded: true },
             { name: "Graveyard", color: "#a3a189", expanded: true },
            ],
      pdfColors: {
              "Acquired Land": "#42f58d",
              "Unacquired Land": "#f54242",
              "Drainage Pipe": "#036bfc",
              "Drainage Direction": "#fceb03",              
            },  
      icon_DR_HouseConnections: L.icon({
              iconUrl: 'HouseConnection.png',
              iconSize: [20, 20],
              iconAnchor: [10, 10]
            }),
    };
  },
  computed: {
    ...mapGetters([
      "getPlotInfo",
      "getPlots",
      "getDeals",
      "getZoom",
      "getCenter",
      "getLabels",
      "getSelectedPlot",
      "getInfoDialog",
      "getLayerVisibility",
      "getPlotVisibility",
      "getUtilityVisibility",
      "getWS_Visibility",
      "getEL_Visibility",
      "getDR_Visibility",
    ]),
      districtList() {
          return [...new Set(this.DR_HouseConnections.features.map(feat => feat.properties.Province))];
      },
      typeList() {
          if (this.selectedDistrict) {
              return [...new Set(this.filteredFeatures.map(feat => feat.properties.District))];
          } else {
              return [...new Set(this.DR_HouseConnections.features.map(feat => feat.properties.District))];
          }
      },
      hotspotList() {
          if (this.selectedDistrict || this.selectedType) {
              return [...new Set(this.filteredFeatures.map(feat => feat.properties.Zone))];
          } else {
              return [...new Set(this.DR_HouseConnections.features.map(feat => feat.properties.Zone))];
          }
      },
      filteredFeatures() {
          return this.DR_HouseConnections.features.filter(feature => {
              return (
                  (!this.selectedDistrict || feature.properties.Province === this.selectedDistrict) &&
                  (!this.selectedType || feature.properties.District === this.selectedType) &&
                  (!this.selectedHotspot || feature.properties.Zone === this.selectedHotspot)
              );
          });
      },
    
  },
  methods: {
    
    applyFilter() {
      // any additional logic on filter apply can be added here
      // console.log('Filter applied with values:', {
      //       district: this.selectedDistrict,
      //       type: this.selectedType,
      //       hotspot: this.selectedHotspot
      //   });
    },
    toggleRightDrawer() {
      this.$store.commit("setRightDrawer", !this.getRightDrawer);
    },
    toggleLeftDrawer() {
      this.$store.commit("setLeftDrawer", !this.getLeftDrawer);
    },
    zoomUpdate() {
      this.$store.commit("setZoom", this.$refs.map.mapObject.getZoom());
      // console.log("Zoom updated: ", this.$refs.map.mapObject.getZoom());
    },
     toggleInfoDialog() {
      this.$store.commit("setInfoDialog", !this.getInfoDialog);
    },
    
 


    utilities_approvedstyle(feature, utilities_approved) {
      if (feature== 'utilities_approved') {
        return {
          strokeColor: "#333333",
          strokeWeight: 0.5,
          weight: 1.5,
          color: "#333333",
          opacity: 1,
          fillColor:
            utilities_approved.properties.category != null
              ? this.pdfColors[utilities_approved.properties.category]
              : "#92919c",
          fillOpacity: 1,
         
        };
      }
    },


    reset_map () {
      
        this.$refs.map.mapObject.flyTo(
        [31.85368251099785,72.90000000048795],
        5
      );
      
    },



    fetchPlotInfo(plot_approved) {
      // var selected_record=plot_approved;
      // console.log("selected_record",selected_record);
      var polygon = plot_approved.geometry;
      var center = turf.centroid(polygon);
      this.$refs.map.mapObject.flyTo(
        [center.geometry.coordinates[1], center.geometry.coordinates[0]],
        18
      );
      this.$store.dispatch("fetchPlotInfo", {
        // url: this.api.url,
        // id: plot.new_id
        info: plot_approved,
      });
      // this.$store.commit("setPlotInfoSkeleton", true);
      this.$store.commit("setInfoDialog", !this.getInfoDialog);

      this.$store.dispatch("markerpoint", {
        lat: center.geometry.coordinates[1],
        lon: center.geometry.coordinates[0],
      });
    },

    fetchKhasraInfo(khasra) {
      // var selected_record=plot_approved;
      // console.log("selected_record",selected_record);
      var polygon = khasra.geometry;
      var center = turf.centroid(polygon);
      this.$refs.map.mapObject.flyTo(
        [center.geometry.coordinates[1], center.geometry.coordinates[0]],
        18
      );
      this.khasra_dialog=true;
      // this.$store.dispatch("fetchPlotInfo", {
      //   // url: this.api.url,
      //   // id: plot.new_id
      //   info: plot_approved,
      // });
      // this.$store.commit("setPlotInfoSkeleton", true);
      // this.$store.commit("setInfoDialog", !this.getInfoDialog);

      this.$store.dispatch("markerpoint", {
        lat: center.geometry.coordinates[1],
        lon: center.geometry.coordinates[0],
      });
    },



    


    fetchPlotInfo_byselect() {
     var plot = this.map.plots_approved.features.filter(item => { 
     if(this.plotID === item.properties.plot_id) { 
          return item;
      }
        });
    var geojson = plot[0].geometry;
    var ring = geojson.coordinates[0][0];
    var poly = turf.polygon([ring]);
    var centroid = turf.centroid(poly);
    // console.log("centroid:",centroid)

    this.$refs.map.mapObject.flyTo(
        [centroid.geometry.coordinates[1], centroid.geometry.coordinates[0]],
        18
      );
          this.$store.dispatch("markerpoint", {
        lat: centroid.geometry.coordinates[1],
        lon: centroid.geometry.coordinates[0],
      });
     },

    

    fetchKhasraInfo_byselect() {
     var khasra = this.map.khasras.features.filter(item => { 
     if(this.khasraID === item.properties.KhasraNo) { 
          return item;
      }
        });
    var geojson = khasra[0].geometry;
    var ring = geojson.coordinates[0][0];
    var poly = turf.polygon([ring]);
    var centroid = turf.centroid(poly);
    // console.log("centroid:",centroid)

    this.$refs.map.mapObject.flyTo(
        [centroid.geometry.coordinates[1], centroid.geometry.coordinates[0]],
        18
      );
          this.$store.dispatch("markerpoint", {
        lat: centroid.geometry.coordinates[1],
        lon: centroid.geometry.coordinates[0],
      });


    },


  },
  
  mounted() {
    this.$store.commit("setMap", this.$refs.map.mapObject);

  }
};
</script>

